.Demo {
    display: flex;
    flex-flow: row wrap;
    gap: 10px 20px;
    justify-content: center;
    align-items: center;
    padding: 20px 0 20px;
}

.Demo__imgs {}

.Demo__img {
    display: block;
    max-width: 150px;
    max-height: 150px;
    cursor: pointer;
    user-select: none;
}

.Demo__img:hover {
    box-shadow: 2px 2px 4px 0px #000;
}

@media (max-width: 150px) {
    .Demo__img {
        max-width: 100%;
    }
}
